:root {
  // base
  --font-family: "Atyp Variable", sans-serif;
  --second-family: "Atyp Display", sans-serif;
  --third-family: "Unbounded", sans-serif;
  --font3: "Manrope", sans-serif;

  --content-width: 1320px;
  --container-offset: 40px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff;
  --seraya-obvodka: #eaeaea;
  --text-color: #444;
  --accent: #2f7af6;
  --light-grey: #bdbdbd;
  @media (max-width: 768px) {
    --container-offset: 10px;
  }
}
